/* ---------------burger */
.burger {
  display: none;
  position: relative;
  width: 24px;
  height: 15px;
  z-index: 1;
  border: none;
  outline: none;
  background: none; }
  @media (max-width: 1023px) {
    .burger {
      display: block; } }
  .burger span {
    position: absolute;
    top: 6px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #44593D;
    border-radius: 10px;
    transition: all .7s ease 0s; }
  .burger::before, .burger::after {
    content: '';
    position: absolute;
    right: 0;
    height: 2px;
    width: 100%;
    background-color: #44593D;
    border-radius: 10px; }
  .burger::before {
    top: 0; }
  .burger::after {
    bottom: 0; }

.burger_active span {
  opacity: 0; }

.burger_active::before, .burger_active::after {
  animation-duration: .7s;
  animation-fill-mode: forwards; }

.burger_active::before {
  animation-name: burger1; }

.burger_active::after {
  animation-name: burger2; }

@keyframes burger1 {
  0% {
    top: 0px; }
  40% {
    transform: rotate(0deg);
    top: 6px; }
  100% {
    transform: rotate(45deg);
    top: 6px; } }

@keyframes burger2 {
  0% {
    bottom: 0; }
  40% {
    transform: rotate(0deg);
    bottom: 7px;
    width: 100%; }
  100% {
    transform: rotate(-45deg);
    bottom: 7px;
    width: 100%; } }

.burger_finish::before, .burger_finish::after {
  animation-duration: .7s;
  animation-fill-mode: forwards; }

.burger_finish::before {
  animation-name: burger3; }

.burger_finish::after {
  animation-name: burger4; }

@keyframes burger3 {
  0% {
    transform: rotate(45deg);
    top: 6px; }
  40% {
    transform: rotate(0deg);
    top: 6px; }
  100% {
    top: 0; } }

@keyframes burger4 {
  0% {
    transform: rotate(-45deg);
    bottom: 6px;
    width: 100%; }
  40% {
    transform: rotate(0deg);
    bottom: 6px;
    width: 100%; }
  100% {
    bottom: 0; } }

